export default {
	data() {
		return {
			oppList: [],
		}
	},
	onLoad: function() {
		uni.request({
			url: 'http://'+getApp().globalData.urlStr+'/OpponentList',
			method: 'GET',
			data: {},
			success: res => {
				if (res.data.code == 0) {
					this.oppList = res.data.data;
				}
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		cancelOpp(){
			uni.reLaunch({
				url:"../main/main"
			})
		},
		selectOpp(e) {
			uni.showModal({
				content: '确定选择该对手吗！',
				showCancel: true,
				success: function(res) {
					if (res.confirm) {
						uni.request({
							url: 'http://'+getApp().globalData.urlStr+'/ChooseOpponent',
							method: 'GET',
							data: {
								oppId: e,
							},
							success: res => {
								if (res.data.code == 0) {
									uni.showModal({
										content: "绑定成功！",
										showCancel: false,
										success: function(res) {
											if (res.confirm) {
												uni.switchTab({
													url: '../main/main',
												});
											}
										}
									})
								}
							},
							fail: () => {},
							complete: () => {}
						});
					}
				}
			});
		}
	}
}
